import { getItem, setItem } from "@/helpers/persistanceStorage";
import JudgingAPi from "@/api/judging";

const state = {
  isLoading: false, // 
  validationErrors: null, //
  players: getItem("players") || "",
  competition: null,
  nomination: null,
  allResults: null,
  nowCompetition: getItem("nowCompetition") || "",
};

export const mutationTypes = {
  // getJudPlayersStart: "[judging] getJudPlayersStart",
  // getJudPlayersSuccess: "[judging] getJudPlayersSuccess",
  // getJudPlayersFailure: "[judging] getJudPlayersFailure",

  getCompetitionStart: "[judging] getCompetitionStart",
  getCompetitionSuccess: "[judging] getCompetitionSuccess",
  getCompetitionFailure: "[judging] getCompetitionFailure",

  getNominationStart: "[judging] getNominationStart",
  getNominationSuccess: "[judging] getNominationSuccess",
  getNominationFailure: "[judging] getNominationFailure",

  postIDCompetitionStart: "[judging] postIDCompetitionStart",
  postIDCompetitionSuccess: "[judging] postIDCompetitionSuccess",
  postIDCompetitionFailure: "[judging] postIDCompetitionFailure",

  setResultStart: "[judging]  setResultStart",
  setResultSuccess: "[judging]  setResultSuccess",
  setResultFailure: "[judging]  setResultFailure",

  getAllResultsStart: "[judging]  getAllResultsStart",
  getAllResultsSuccess: "[judging] getAllResultsSuccess",
  getAllResultsFailure: "[judging]  getAllResultsFailure",
};
export const actionTypesJudgind = {
  getCompetition: "[judging] getCompetition",
  getNomination: "[judging] getNomination",
  setResult: "[judging] setResult",
  postIDCompetition: "[judging] postIDCompetition",
  getAllResults: "[judging] getAllResults",
};

const mutations = {
  [mutationTypes.getCompetitionStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.getCompetitionSuccess](state, payload) {
    state.isLoading = false;
    state.competition = payload;
  },
  [mutationTypes.getCompetitionFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.getNominationStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.getNominationSuccess](state, payload) {
    state.isLoading = false;
    state.nomination = payload;
  },
  [mutationTypes.getNominationFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.postIDCompetitionStart](state, payload) {
    state.isLoading = true;
    state.nowCompetition = payload;
  },
  [mutationTypes.postIDCompetitionSuccess](state, payload) {
    state.isLoading = false;
    state.players = payload;
  },
  [mutationTypes.postIDCompetitionFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.setResultStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.setResultSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.setResultFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getAllResultsStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.getAllResultsSuccess](state, payload) {
    state.isLoading = false;
    state.allResults = payload;
  },
  [mutationTypes.getAllResultsFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  //     
  // [actionTypesJudgind.getJudPlayers](context) {
  //   return new Promise((resolve) => {
  //     context.commit(mutationTypes.getJudPlayersStart);
  //     JudgingAPi.getJudPlayers()
  //       .then((response) => {
  //         if (response.data.status === "SUCCESS") {
  //           setItem("players", response.data.result);
  //           context.commit(
  //             mutationTypes.getJudPlayersSuccess,
  //             response.data.result
  //           );
  //         } else {
  //           context.commit(mutationTypes.getJudPlayersFailure, response);
  //         }
  //         resolve(response.data);
  //       })
  //       .catch((response) => {
  //         context.commit(mutationTypes.getJudPlayersFailure, response);
  //         console.log("", response);
  //       });
  //   });
  // },
  //  
  [actionTypesJudgind.getCompetition](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getCompetitionStart);
      JudgingAPi.getCompetition()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(
              mutationTypes.getCompetitionSuccess,
              response.data.result
            );
          } else {
            context.commit(mutationTypes.getCompetitionFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.getCompetitionFailure, response);
          console.log("", response);
        });
    });
  },
  //    
  [actionTypesJudgind.getNomination](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getNominationStart);
      JudgingAPi.getNomination()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(
              mutationTypes.getNominationSuccess,
              response.data.result
            );
          } else {
            context.commit(mutationTypes.getNominationFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.getNominationFailure, response);
          console.log("", response);
        });
    });
  },
  //   
  [actionTypesJudgind.postIDCompetition](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.postIDCompetitionStart, credentials);
      setItem("nowCompetition", credentials);
      JudgingAPi.postIDCompetition(credentials)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            setItem("players", response.data.result);
            context.commit(
              mutationTypes.postIDCompetitionSuccess,
              response.data.result
            );
          } else {
            context.commit(mutationTypes.postIDCompetitionFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.postIDCompetitionFailure, response);
          console.log("", response);
        });
    });
  },
  //   
  [actionTypesJudgind.setResult](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.setResultStart);
      JudgingAPi.setResult(credentials)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(
              mutationTypes.setResultSuccess,
              response.data.result
            );
          } else {
            context.commit(mutationTypes.setResultFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.setResultFailure, response);
          console.log("", response);
        });
    });
  },
  //  
  [actionTypesJudgind.getAllResults](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAllResultsStart);
      JudgingAPi.getAllResults()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(
              mutationTypes.getAllResultsSuccess,
              response.data.result
            );
          } else {
            context.commit(mutationTypes.getAllResultsFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.getAllResultsFailure, response);
          console.log("", response);
        });
    });
  },
};

export default {
  state,
  mutations,
  actions,
};
