                rx="15.4782"
              />
              <path
                class="login-cls-5"
                d="M60.4945,69.7122,50.715,53.4074c-.0294-6.4277-.4729-10.769-1.6225-13.8621,1.4422-.5915,5.7823-2.4807,7.37-5.3617a1.9915,1.9915,0,0,0,.1877-.4944c1.0619-4.42-.9521-7.6269-4.2659-10.3216-2.1084-2.1195-5.8135-.0152-3.1779,2.6342,5.5434,4.6693,4.6439,7.6238-2.6354,9.4976-.18-.18-.3652-.354-.5593-.5213-1.4756-1.2086-3.701.5188-2.6481,2.1071.03.04.0555.0818.0849.122a62.02,62.02,0,0,0-8.1995,6.3169,1.7652,1.7652,0,0,0-.4874,1.1216l0,.0091a2.558,2.558,0,0,0,1.6835,2.4941L46.651,51.2156q-.0311,1.1439-.1043,2.37L37.3905,69.7809c-1.68,3.3668,1.6582,5.0612,3.337,2.2562l7.9446-14.3626c.0052,0,.01,0,.0156,0l8.9758,14.8085C59.3518,74.7109,62.7446,73.0525,60.4945,69.7122ZM40.4152,44.31c1.7629-1.2052,3.3086-2.411,4.9155-3.6166a21.6345,21.6345,0,0,1,1.2545,6.2688L40.4436,44.59A.16.16,0,0,1,40.4152,44.31Z"
