<template>
  <div class="score">
    <div class="wrap-loader" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <div class="table-score" v-if="this.infoUser.role === 'ROLE_DIF'">
      <div class="table-score__judge">
          {{ this.infoUser.brigade }} {{ this.infoUser.position }} 
        {{ this.infoUser.positionNumber }}
      </div>
      <div class="table-score__number">
         <span>{{ this.players.startId }}</span>
      </div>
      <div class="table-score__wrap">
        <div class="table-score__nomination">
          : <span>{{ this.players.nomination }}</span>
        </div>
        <div class="table-score__age">
          : <span> {{ this.players.age }}</span>
        </div>
      </div>
      <div class="table-score__wrap2">
        <div
          class="table-score__fio"
          v-for="(sportsmen, index) in this.players.fio"
          :key="index"
        >
          {{ sportsmen.fio }}
        </div>
      </div>
      <div class="info__input">
        <label for="exe">:</label>
        <input
          type="number"
          id="item"
          name="art"
          max="10"
          placeholder="10.0"
          v-model="scoreDif"
          required
          aria-label=""
          class="input-search"
        />
      </div>

      <p class="table-score__title">:</p>

      <div class="info__input-dif">
        <div class="info__wrap">
          <label for="elements"></label>
          <input
            type="number"
            id="item"
            name="elements"
            max="10"
            placeholder="10.0"
            v-model="elements"
            aria-label=""
            class="input-search"
          />
        </div>
        <div class="info__wrap">
          <label for="line"></label>
          <input
            type="number"
            id="item"
            name="line"
            max="10"
            placeholder="10.0"
            v-model="line"
            aria-label=""
            class="input-search"
          />
        </div>
      </div>
      <div class="info-score__btn">
        <input
          type="submit"
          value=""
          class="button btn-primary"
          @click="onSubmit()"
          :disabled="!this.scoreDif"
        />
      </div>
    </div>
    <div class="table-score__error" v-else>
      <p class="error-info">,     !</p>
      <input
        type="submit"
        value=" "
        class="button btn-primary"
        @click="backRoute()"
      />
    </div>
  </div>
</template>

<script>
import { actionTypesJudgind } from "@/store/modules/judging";
import { mapState } from "vuex";
export default {
  name: "AppDif",
  data() {
    return {
      scoreDif: "",
      elements: "",
      main: "",
      line: "",
    };
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      players: (state) => state.judging.players,
      nowCompetition: (state) => state.judging.nowCompetition,
      isLoading: (state) => state.judging.isLoading,
    }),
  },
  methods: {
    onSubmit() {
      this.players.fio.forEach((item) => this.idSportsmen.push(item.id));
      this.infoScore = {
        competition: this.nowCompetition,
        startId: this.players.startId,
        score: this.scoreDif,
        idPerson: this.idSportsmen,
      };
      console.log(this.infoScore);
      this.$store
        .dispatch(actionTypesJudgind.setResult, this.infoScore)
        .then(() => {
          this.score = false;
          this.isSubmitScore = true;
          this.idSportsmen = [];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    backRoute() {
      this.$router.push({ name: "Index" });
    },
  },
};
</script>

<style></style>
